  top: 0;
  left: 0;
  bottom: 0;
  right: 0; }

.float-right {
  float: right; }

.text-shadow {
  text-shadow: 2px 2px 4px #999999;
  &-black {
    text-shadow: 2px 2px 4px #000000; }
  &-white {
    text-shadow: 2px 2px 4px #000000; } }

.text-bold {
  font-weight: bold; }
.text-spacing {
  letter-spacing: 0.4em; }

.animation-shaking {
  animation: shake 0.82s infinite ease-in-out;
  transform: translate3d(2px, 0, 0); }
@keyframes shake {
  from {
    transform: translate3d(2px, 0, 0); }

  50% {
    transform: translate3d(-2px, 0, 0); }

  to {
    transform: translate3d(2px, 0, 0); } }

.bg-warning2 {
  background-color: $highlight; }

.cursor-help {
  cursor: help; }

.parent-hovershow {
  .hovershow {
    &-inline {
      display: none; }
    &-block {
      display: none; } }
  &:hover {
    .hovershow {
      &-inline {
        display: inline-block; }
      &-block {
        display: block; } } } }

.underline-onhover {
  &:hover {
    text-decoration: underline !important; } }
