/* Особые чекбоксы */
/* https://bootsnipp.com/snippets/featured/simple-css-custom-checkboxes-and-radios */
.form-check label {
  cursor: pointer;
  position: relative;
  padding-left: 1.7em; }

.form-check input[type="checkbox"], .form-check input[type="radio"] {
  position: absolute;
  display: none;
  opacity: 0; }

.form-check input[type="checkbox"] + .label-text {
  font-weight: normal; }

.form-check input[type="checkbox"] + .label-text:before {
  content: "\f111";
  font-family: "Font Awesome 5 Free";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  font-size: 120%;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  width: 1em;
  display: inline-block;
  margin-right: 0.4em;
  margin-left: -1.4em;
  color: #999999;
  background-color: #FFFFFF;
  border-radius: 50%; }

.form-check input[type="checkbox"]:checked + .label-text:before {
  content: "\f058";
  color: #1422aa;
  animation: effect 250ms ease-in; }

.form-check input[type="checkbox"]:disabled + .label-text {
  color: #aaa; }
  .form-check input[type="checkbox"]:disabled + .label-text:before {
    content: "\f058";
    color: #ccc; }

.form-check input[type="radio"] + .label-text {
  font-weight: normal; }

.form-check input[type="radio"] + .label-text:before {
  content: "\f111";
  font-family: "Font Awesome 5 Free";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  font-size: 120%;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  width: 1em;
  display: inline-block;
  margin-right: 0.4em;
  margin-left: -1.4em;
  color: #999999;
  background-color: #FFFFFF;
  border-radius: 50%; }

.form-check input[type="radio"]:checked + .label-text:before {
  content: "\f192";
  color: #1422aa;
  animation: effect 250ms ease-in; }

.form-check input[type="radio"]:disabled + .label-text {
  color: #aaa; }
  .form-check input[type="radio"]:disabled + .label-text:before {
    content: "\f111";
    color: #ccc; }

.form-check .toggle input[type="radio"] + .label-text:before {
  content: "\f204";
  font-family: "Font Awesome 5 Free";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  width: 1em;
  display: inline-block;
  margin-right: 10px;
  color: #999999; }

.form-check .toggle input[type="radio"]:checked + .label-text:before {
  content: "\f205";
  color: #16a085;
  animation: effect 250ms ease-in; }

.form-check .toggle input[type="radio"]:disabled + .label-text {
  color: #aaa; }
  .form-check .toggle input[type="radio"]:disabled + .label-text:before {
    content: "\f204";
    color: #ccc; }

@keyframes effect {
  0% {
    transform: scale(0); }
  25% {
    transform: scale(1.3); }
  75% {
    transform: scale(1.4); }
  100% {
    transform: scale(1); } }

